import { POSITIONS } from '../../utilities/axis';
import { getLeft, getTop } from '../../utilities/element';
import { scroll } from '../../api/scroll';
import { scrollTo } from '../../utilities/scroll';
const isNodeJS = typeof window === 'undefined';
function getOffsetByAxis({ el, axis }) {
    if (axis === 'y')
        return getTop(el);
    return getLeft(el);
}
function getScrolledByAxis({ el, axis }) {
    if (axis === 'y')
        return el.scrollTop;
    return el.scrollLeft;
}
export function fitOnScreen({ elRelative = !isNodeJS
    ? document.querySelector('[data-ovo-fs="el"]') || document
    : undefined, elsToFit: externalElsToFit, proximityToFit = 240, axis = 'y', debounce = 1000, limit = POSITIONS, }) {
    if (!elRelative)
        return;
    const elsToFit = externalElsToFit ||
        Array.from(document.querySelectorAll('[data-ovo-fs="content"]'));
    const isNearOfElement = ({ elToFit, scrolledPosition }) => {
        const offsetElToFit = getOffsetByAxis({ axis, el: elToFit });
        const highOffsetElToFit = offsetElToFit - proximityToFit;
        const lowOffsetElToFit = offsetElToFit + proximityToFit;
        const betweenLowHigh = scrolledPosition > highOffsetElToFit &&
            scrolledPosition < lowOffsetElToFit;
        return betweenLowHigh;
    };
    function getNearElement({ scrolledPosition = 0 }) {
        return elsToFit.find((elToFit) => isNearOfElement({ elToFit, scrolledPosition }));
    }
    function handleScroll({ el }) {
        const nearElement = getNearElement({
            scrolledPosition: getScrolledByAxis({ el, axis }),
        });
        if (!nearElement)
            return;
        scrollTo({
            top: getOffsetByAxis({ axis, el: nearElement }),
            left: getOffsetByAxis({ axis, el: nearElement }),
            scrollingElement: el,
        });
    }
    function bindEvents() {
        const observable = scroll({
            el: elRelative,
            debounce,
            limit,
        });
        observable.subscribe(handleScroll);
    }
    bindEvents();
}
function autoStart() {
    if (isNodeJS)
        return;
    const flag = document.querySelector('[data-ovo-fs][data-ovo-auto]');
    if (!flag)
        return;
    fitOnScreen({});
}
autoStart();
