// eslint-disable-next-line import/no-unresolved
import { isNumber } from 'lodash';
import { pipe } from 'rxjs';
import { filter, map, pairwise, scan } from 'rxjs/operators';
import { AXES, getDirection, isOnGap, } from '../../utilities/axis';
export function filterByAttributeAndGapOperator({ gap = AXES, }) {
    return pipe(map((scrollObserver) => {
        return {
            current: scrollObserver,
            last: scrollObserver,
        };
    }), scan((acc, curr) => {
        const axes = acc.current.axes;
        const lastAxes = acc.last.axes;
        const onGap = isOnGap({
            axes,
            gap,
            lastAxes,
        });
        return {
            current: curr.current,
            last: onGap ? acc.last : curr.current,
        };
    }), filter(({ current, last }, index) => {
        const axes = current.axes;
        const lastAxes = last.axes;
        const firstEvent = !index;
        const isToIgnore = current.type === 'START';
        if (firstEvent || isToIgnore)
            return true;
        return !isOnGap({
            axes,
            gap,
            lastAxes,
        });
    }), map((scrollObserver) => {
        return scrollObserver.current;
    }));
}
export function putRelativeAxesOperator({ stopGrowingAt = 0, }) {
    return pipe(scan((acc, curr, index) => {
        const firstRun = index === 1;
        const stopGrowingAtAsPositions = stopGrowingAt;
        const toRestart = curr.type === 'START';
        let { startAxes } = acc;
        let relativeStartAxes = acc.relatives.startAxes;
        const currAxes = curr.axes;
        if (firstRun || toRestart) {
            startAxes = currAxes;
            relativeStartAxes = currAxes;
        }
        if (stopGrowingAtAsPositions.bottom &&
            stopGrowingAtAsPositions.left &&
            stopGrowingAtAsPositions.top &&
            stopGrowingAtAsPositions.right) {
            if (currAxes.x < relativeStartAxes.x - stopGrowingAtAsPositions.left) {
                relativeStartAxes.x = currAxes.x + stopGrowingAtAsPositions.left;
            }
            if (currAxes.y < relativeStartAxes.y - stopGrowingAtAsPositions.top) {
                relativeStartAxes.y = currAxes.y + stopGrowingAtAsPositions.top;
            }
            if (currAxes.x > relativeStartAxes.x + stopGrowingAtAsPositions.right) {
                relativeStartAxes.x = currAxes.x - stopGrowingAtAsPositions.right;
            }
            if (currAxes.y >
                relativeStartAxes.y + stopGrowingAtAsPositions.bottom) {
                relativeStartAxes.y = currAxes.y - stopGrowingAtAsPositions.bottom;
            }
        }
        let relativeX = currAxes.x - relativeStartAxes.x;
        let relativeY = currAxes.y - relativeStartAxes.y;
        const hypotenuse = Math.hypot(relativeX, relativeY);
        if (isNumber(stopGrowingAt)) {
            const isOutOfRadix = hypotenuse > stopGrowingAt;
            if (isOutOfRadix) {
                const sin = relativeY / hypotenuse;
                const cos = relativeX / hypotenuse;
                relativeStartAxes.x =
                    cos * (hypotenuse - stopGrowingAt) +
                        relativeStartAxes.x;
                relativeStartAxes.y =
                    sin * (hypotenuse - stopGrowingAt) +
                        relativeStartAxes.y;
                relativeX = cos * stopGrowingAt;
                relativeY = sin * stopGrowingAt;
            }
        }
        const angle = ((Math.atan2(-relativeY, relativeX) * 180) / Math.PI + 360) % 360;
        return Object.assign(Object.assign({}, curr), { startAxes, relatives: Object.assign(Object.assign({}, curr.relatives), { axes: {
                    x: relativeX,
                    y: relativeY,
                }, startAxes: relativeStartAxes, angle }) });
    }));
}
export function putAxesBreakpointOperator({ gap = AXES, }) {
    return pipe(map((value) => {
        const { axes } = value.relatives;
        const x = Math.floor(axes.x / gap.x || 1) - 1;
        const y = Math.floor(axes.y / gap.y || 1) - 1;
        return Object.assign(Object.assign({}, value), { relatives: Object.assign(Object.assign({}, value.relatives), { breakpointAxes: {
                    x,
                    y,
                } }) });
    }));
}
export function putDirectionOperator(k, directionK, ignoreWhen) {
    return pipe(pairwise(), map(([last, curr]) => {
        const lastAxes = last[k];
        const currAxes = curr[k];
        let direction = last[directionK] || '';
        const isToIgnore = curr[ignoreWhen.key] === ignoreWhen.value;
        if (!isToIgnore) {
            direction = getDirection({
                axes: currAxes,
                lastAxes,
            });
        }
        return Object.assign(Object.assign({}, curr), { [directionK]: direction });
    }));
}
